<template>
	<div class="outer">
		<navigation :active-index='index'></navigation>
		<layout></layout>
		<page-footer></page-footer>
	</div>
</template>

<script>
	import '../static/css/reset1.2.css'
	import Layout from './components/course/Layout.vue'
	import Nav from './components/public/Nav.vue'
	import Footer from './components/public/Footer.vue'
	export default {
		name: 'articles',
		components: {
			'layout': Layout,
			'page-footer': Footer,
			'navigation': Nav,
		},
		data: () => {
			return {
				index: "2",
			}
		}
	}
</script>

<style>
	.outer {
		min-width: 800px;
	}
</style>


