<template>
	<div class="course">
		<div class="title-background" v-bind:style="{'background-image':'url('+(course.imgUrl&&course.imgUrl)+')'}">
			<!--<el-breadcrumb separator="/">
				<el-breadcrumb-item>主页</el-breadcrumb-item>
				<el-breadcrumb-item>课程学习</el-breadcrumb-item>
			</el-breadcrumb>-->
			<div class="c-title">
				{{course.name}}
			</div>
		</div>

		<el-row>
			<el-col :span="16">
				<div class="c-describe">
					简介：{{course.description}}
				</div>
				<el-collapse>
					<el-collapse-item v-for="(s,i) in course.sections" v-bind:title="(i+1+'.')+s.name" v-bind:name="i">
						{{s.description}}
						<div class="labels">
							<span v-for="tag in s.tags">
							{{tag.name}}
							</span>
						</div>
						<div class="articles-title">
							<div v-for="tag in s.tags">
								<a v-bind:href="'/goArticle/'+a.id" target="view_window" v-for="a in tag.articles">{{a.title}}</a>
								<!--<span class="getMore-articles-title" title="获取更多">-->
							<!--获取更多<i class="el-icon-caret-bottom"></i>-->
							
						</span></div>
						</div>
					</el-collapse-item>
				</el-collapse>
			</el-col>
			<el-col :span="8">
				<my-aside v-bind:prompt-content="course.prompt"></my-aside>
			</el-col>
		</el-row>
	</div>
</template>
<script>
	import { Collapse, CollapseItem, Row, Col, Breadcrumb, BreadcrumbItem } from 'element-ui'
	import Aside from './Aside.vue'
	export default {
		name: 'articles',
		components: {
			'el-collapse': Collapse,
			'el-collapse-item': CollapseItem,
			'el-col': Col,
			'el-row': Row,
			'my-aside': Aside,
			'el-breadcrumb': Breadcrumb,
			'el-breadcrumb-item': BreadcrumbItem
		},
		created() {
			var urlStr = location.href;
			var id = urlStr.substring(urlStr.lastIndexOf("/") + 1, urlStr.length);
			console.log(id);
			this.getCourse(id);
		},
		data() {
			return {
				course: {},
			};
		},
		methods: {
			getCourse(id) {
				this.$http.get('/course/get/' + id).then((response) => {
					console.log(response.data);
					this.course = response.data;
				}, (response) => {
					console.log('获取课程失败');
				});
			},
		}
	}
</script>
<style>
	.course {
		margin: 0 auto;
		min-height: 800px;
	}
	
	.title-background {
		height: 200px;
		width: 100%;
		background-color: rgba(0, 0, 0, .4);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
	}
	
	.el-breadcrumb {
		max-width: 1200px;
		min-width: 800px;
		margin: 0 auto;
		padding: 10px 0;
	}
	
	span.el-breadcrumb__item__inner {
		color: #ccc;
	}
	
	.el-breadcrumb__item span.el-breadcrumb__item__inner:hover,
	.el-breadcrumb__item:last-child span.el-breadcrumb__item__inner,
	.el-breadcrumb__item:last-child span.el-breadcrumb__item__inner:hover {
		/*color: #FFFFFF;*/
	}
	
	.c-title {
		font-size: 48px;
		line-height: 137px;
		color: #FFFFFF;
	}
	
	.c-title,
	.el-row {
		max-width: 1200px;
		min-width: 800px;
		margin: 0 auto;
	}
	
	.c-describe {
		font-size: 14px;
		font-weight: 400;
		color: #888;
		padding: 30px 0 30px;
		line-height: 30px;
	}
	/*.section {
		font-size: 14px;
		background-color: #f3f5f7;
		margin: 10px 0 10px 35px;
		padding: 8px;
		color: #787d82;
	}
	
	.section:hover {
		background: #2b333b;
	}
	
	div.section:hover a {
		color: #fff;
	}
	*/
	
	div.el-collapse {
		border-left-width: 0;
		border-right-width: 0;
	}
	
	div.el-collapse-item__header {
		padding: 7px 10px 7px 10px;
		font-weight: 700;
		font-size: 14px;
	}
	
	.course .labels {
		margin: 10px 0 5px;
	}
	
	.course .labels span {
		display: inline-block;
		font-size: 13px;
		border: 1px solid #20A0FF;
		padding: 0 8px;
		margin-right: 5px;
		border-radius: 40px;
		cursor: pointer;
		color: #1D8CE0;
	}
	
	.articles-title a {
		display: block;
		font-size: 16px;
		width: 100%;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		margin: 3px 0;
	}
	
	.articles-title a:hover {
		color: #888;
	}
	
	.getMore-articles-title {
		cursor: pointer;
		display: block;
		width: 70px;
		margin: 0 auto;
		font-size: 14px;
		transition: color ease .3s;
	}
	
	.getMore-articles-title:hover {
		color: #999;
	}
</style>


